import common from '../../common/common.js'
import {
	uniRate
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			swiperList: [],
			hotSuperheroList: [],
			trailerSuperheroList: [],
			animationData: {},
			praiseList: [],
			animationDataArr: [{}, {}, {}, {}, {}]
		}
	},
	onLoad() {

		this._setAnimation()

		this._getSwiperData()
	},
	onUnload() {
		// 页面卸载的时候清除动画数据
		// this.animationData = {}
		this.animationDataArr = [{}, {}, {}, {}, {}]
	},
	onHide() {
		if (this.videoContext) {
			this.videoContext.pause();
		}
	},
	onPullDownRefresh() {
		this._getSwiperData()
	},
	methods: {
		// 点击操作
		praiseMe(e) {

			const index = e.currentTarget.dataset.index

			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-70).opacity(1).step({
				duration: 400
			})
			this.animationData = this.animation
			this.animationDataArr[index] = this.animationData.export()

			// 执行完毕动画以后肯定是要复原的
			setTimeout(() => {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				})
				this.animationData = this.animation
				this.animationDataArr[index] = this.animationData.export()
			}, 500)

		},
		// 设置动画
		_setAnimation() {
			this.animation = uni.createAnimation()
		},
		// 得到数据
		_getSwiperData() {
			uni.showLoading({
				title: '加载中',
				mask: true
			});
			uni.request({
				url: common.api_base_url + '/index/carousel/list',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: common.qq
				},
				method: 'POST',
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.swiperList = res.data.data
					} else {
						this.swiperList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.swiperList = []
				}),
				complete: (() => {
					this._getSuperheroData()
				})
			})
		},
		_getSuperheroData() {
			wx.request({
				url: common.api_base_url + '/index/movie/hot?type=superhero',
				method: 'POST',
				data: {
					qq: common.qq
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.hotSuperheroList = res.data.data
					} else {
						this.hotSuperheroList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.hotSuperheroList = []
				}),
				complete: (() => {
					this._getTrailderSuperheroData()
				})
			})
		},
		_getTrailderSuperheroData() {
			uni.request({
				url: common.api_base_url + '/index/movie/hot?type=trailer',
				method: 'POST',
				data: {
					qq: common.qq
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.trailerSuperheroList = res.data.data
					} else {
						this.trailerSuperheroList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.trailerSuperheroList = []
				}),
				complete: (() => {
					this._getPraiseData()
				})
			})
		},
		_getPraiseData() {
			uni.request({
				url: common.api_base_url + '/index/guessULike',
				method: 'POST',
				data: {
					qq: common.qq
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.praiseList = res.data.data
					} else {
						this.praiseList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.praiseList = []
				}),
				complete: (() => {
					uni.hideLoading()
					uni.stopPullDownRefresh()
				})
			})
		},
		gotoMoviePage(e) {
			const id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: `../movie/movie?id=${id}`
			})
		},
		// 这段视频播放的时候，让其余的视频都暂停
		meIsPlaying(e) {
			let id = ''
			if (e) {
				id = e.currentTarget.dataset.playingindex;
				this.videoContext = uni.createVideoContext(id)
			}
			let trailerSuperheroList = this.trailerSuperheroList
			for (const item of trailerSuperheroList) {
				if (item.id !== id) {
					uni.createVideoContext(item.id).pause();
				}
			}
		}
	},
	components: {
		uniRate
	}
}
